.post-tile a{
    text-decoration: none;
}

.post-tile h2{
    text-decoration: none;
    font-weight: bold;
}

.post-tile a:hover{
    .post-tile-text h2{
        color: var(--site-main-color2);
    }
}

// CONTENT (IMAGE + TEXT)
.post-tile-content{
    background: white;
    box-shadow: 0rem 0rem 0.25rem rgba(0, 0, 0, 0.25) ;
    position: relative;
    display: grid;
    align-items: center;
    grid-template-columns: minmax(0, 2.5fr) minmax(0, 5fr);
    grid-template-areas: "area1 area2";
}

.post-tile-content:hover{
    box-shadow: 0rem 0rem 0.3rem rgba(var(--site-main-color-rgb), 0.8);
}

@media screen and (max-width: 600px) {
    .post-tile-content {
        grid-template-columns: minmax(0, 1fr);
        grid-template-areas: 
        "area1"
        "area2";
    }
    .post-tile-img-container{
        justify-self: center;
        max-width: 20rem;
    }
}

// IMAGE
.post-tile-img-container{
    grid-area: area1;
    max-height: 12.5rem;
    overflow: hidden;
    display: flex;
    aspect-ratio: 3 / 2;
}

.post-tile-image{
    max-width: 100%;
    width: 100%;
    align-self: center;
}

// TEXT
.post-tile-text{
    align-self: self-start;
    grid-area: area2;
    color: black;
    margin: 1rem;
}

.post-tile-text h2{
    color: var(--site-main-color);
}

.post-tile-text p{
    display: -webkit-box;
    -webkit-line-clamp: 4;
    line-clamp: 4;
    -webkit-box-orient: vertical;  
    margin-block-end: 0;
    text-overflow: ellipsis;
    overflow: hidden;
}

// // META : DATE & TAGS
.post-tile-meta{
    display: block;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    // gap: 0.5rem;
    font-style: italic;
    color: var(--color-gray5);
}

.post-tile-meta i{
    margin-right: 0.25rem;
}

.post-tile-meta #tag-icon{
    margin-left: 1.25rem;
}