
// // META : DATE & TAGS
.post-meta{
    display: block;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    // gap: 0.5rem;
    font-style: italic;
    color: var(--color-gray5);
}

.post-meta a{
    text-decoration: none;
    color: var(--color-gray5);
}

.post-meta a:hover{
    color: var(--site-main-color2);
}

.post-meta i{
    margin-right: 0.25rem;
}

.post-meta #tag-icon{
    margin-left: 1.25rem;
}


.post-page img{
    width: auto;
    max-width: 100%;
    height: auto;
}

.post-page .highlight{
    overflow-x: auto;
    margin-left: 0;
}

.post-page p{
    line-height: 1.5rem;
}

code.language-plaintext{
    background: #eaeaea;
    padding: 0.2rem;
}

/*#region Code highlight*/
pre{ margin: 0; }

.highlight{
    margin-inline-end: 0;
}

.highlight > pre:nth-of-type(1){
    margin: 0.5rem auto;
}

.highlight .lineno{
    vertical-align: top;
}

.highlight pre:not(.lineno){
    margin-left: 0.5rem;
    margin-right: 0.5rem;
}

.highlight .gutter{
    vertical-align: top;
}

code, code pre {
    font-family: "Roboto Mono";
    font-size: 0.81rem;
}
  
/*#region Code highlight*/

/*#region XAML Code color*/
.highlight:has(.language-xaml){
    background: #1F1F1F; //background
}

.language-xaml .hll{
    background: rgb(66 106 76 / 38%); // line highlight
}

.language-xaml .c{
    color: rgb(87,166,74); // comment
}

.language-xaml .p{
    color: rgb(128,128,128); // punctuation
}

.language-xaml .nt{
    color: rgb(230,230,230); // tag name
}

.language-xaml .na{
    color: rgb(146,202,244); // attribute name
}

.language-xaml .s{
    color: rgb(86,156,214); // attribute string value (no binding or brackets)
}

.language-xaml .nc{
    color: rgb(187,160,140); // extension class
}

.language-xaml .py{
    color: rgb(215,186,125); // extension parameter name
}

.language-xaml .nx{
    color: rgb(177,177,177); // extension parameter value
}

.language-xaml .lineno{
    color: rgb(43,145,175); // line numbers
}
/*#endregion XAML Code color*/

/*#region PowerShell Code color*/
// .highlight:has(.language-xaml){
//     background: #1F1F1F; //background
// }

.language-powershell .hll{
    background: rgb(66 106 76 / 38%); // line highlight
}

.language-powershell .c{
    color: rgb(0,100,00); // comment
}

.language-powershell .p{
    color: rgb(128,128,128); // punctuation
}

.language-powershell .nv{
    color: rgb(168,45,0); // variable
}

.language-powershell .o{
    color: rgb(105,105,105); // operator
}

.language-xaml .lineno{
    color: rgb(43,145,175); // line numbers
}
/*#endregion PowerShell Code color*/