:root{
    --sidebar-w: 15.6rem;
    --sidebar-p-l: 1rem;
}

#sidebar{
    max-width: var(--sidebar-w);
    width: 20%;
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
    padding: 1.25rem 0;
}

#sidebar .border{
    border-left: 0.07rem solid var(--color-gray3);
}
#sidebar .padding-left{
    padding-left: var(--sidebar-p-l);
}

#sidebar h1{
    margin-block-start: 0;
    margin-block-end: 0.75rem;
}

#sidebar p{
    margin-block-end: 0;
}

@media screen and (max-width: 700px) {
    #sidebar{
        display: none;
    }
}

//LINKS
#sidebar-links-wrapper{
    display: flex;
    gap: 0.5rem;
    flex-wrap: nowrap;
}

#sidebar .link-icons{
    --size: 2rem;
    fill: var(--site-main-color);
    height: var(--size);
    width: var(--size);
}

#sidebar .link-icons:hover{
    fill: var(--site-main-color2);
}

//TOC
.toc-hidden{
    display: none;
}

#sidebar-toc{
    overflow: hidden;
    position: sticky;
    top: 5rem;
}
#sidebar-toc h1{
    padding-left: var(--sidebar-p-l);
}

.toc a{
    display: flex;
    align-items: center;
    // white-space: nowrap;
    // text-overflow: ellipsis;
    text-decoration: none;
    overflow: hidden;
    padding-left: var(--sidebar-p-l);
}

.toc-list-item{
    list-style:none;
    margin-block-end: 0.4rem;
}

.toc-list-item:last-child{
    margin-block-end: 0;
}

.toc-list{
    margin:0;
    padding-left:0
}

.toc-link{
    color: black;
    position: relative;
    height: auto;
}

.toc-link:hover{
    color: var(--color-gray5)
}

.is-collapsible{
    max-height:1000px;
    overflow:hidden;
    transition:all 300ms ease-in-out
}
.is-collapsed{
    max-height:0
}
.is-active-link{
    font-weight:700;
    color: var(--site-main-color2);
}
.is-active-link:hover{
    color: var(--site-main-color2);
}
.toc-link::before{
    background:transparent;
    content:" ";
    height: 2px;
    left: 0;
    right: calc(100% - 0.8rem);
    margin-top:-1px;
    position:absolute;
    overflow-y: hidden;
}
.is-active-link::before{
    background: linear-gradient(0.25turn,var(--color-gray3),var(--site-main-color2))
}