:root{
    --footer-background: var(--site-main-color);
    --footer-height: 9.5rem;
    --footer-title-padding: 0.5rem;
    --footer-content-color: white;
}

footer{
    display: flex;
    justify-content: center;
    background: var(--site-main-color);
    border-top: solid 0.2rem var(--site-main-color2);
    width: 100%;
    height: var(--footer-height);
    position: relative;
    white-space: nowrap;
}

#footer-title-wrapper{
    display: flex;
    flex-grow: 1;
    height: 100%;
    padding: 1rem;
    box-sizing: border-box;
    justify-content: center;
}

#footer-title-wrapper svg{
    fill: hsl(180, 39%, 15%);
}

#footer-contents{
    display: flex;
    flex-wrap: nowrap;
    gap: 0.5rem;
    height: 100%;
    position: absolute;
    align-items: center;
    font-size: 1.2rem;
    line-height: 100%;
    font-family: "Bebas Neue", sans-serif;
}

#footer-contents span,a {
    color: var(--footer-content-color);
    text-decoration: none;
}

#footer-contents>a:hover {
    text-decoration: underline;
}

#footer-socials{
    display: flex;
    gap: 0.5rem;
    align-items: center;
}

#footer-socials svg{
    flex-wrap: inherit;
    --size: 1.8rem;
    height: var(--size);
    width: var(--size);
    fill: var(--footer-content-color);
}

#footer-socials::before{
    content: "\002B23 ";
    font-size: 0.7rem;
    color: var(--footer-content-color);;
}