:root{
  --header-max-width : var(--mc-max-width);
  --header-border-top-color: var(--site-main-color);
  --header-border-bottom-color: var(--color-gray3);
  --header-background-color: rgb(255, 255, 255);

  --sb-height: 1.875rem;
  --sb-edge-angle: var(--angle);
  --sb-inner-padding: 2px;
  --sb-base-color: var(--color-gray1);
  --sb-hover-color: var(--color-gray2);
  --sb-focus-bd-color: var(--site-main-color);
}

header {
  position: sticky;
  display: flex;
  justify-content: center;
  top: 0;
  width: 100%;
  z-index: 9999;
  border-top: solid var(--site-main-color);
  border-bottom: solid 0.06rem var(--header-border-bottom-color);
  background-color: var(--header-background-color);
}
  
header nav{
  max-width: var(--header-max-width);
  flex: 1;
  gap: 1rem;
  margin: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header a {
  color: var(--site-main-color);
  text-decoration: none;
  font-size: 1.3rem;
  font-family: "Bebas Neue", sans-serif;
  display: flex;
  gap: 0.1rem;
  align-items: center;
}

// .nav-categories.active, .nav-categories:hover{
//   color: var(--site-main-color2);
// }

header span{
  padding-top: 0.2rem;
  line-height: 100%;
}

#header-title {
  font-size: 2.1rem;
  font-style: italic;
  white-space: nowrap;
}

#site-icon{
  width: 2.5rem;
  height: 2.5rem;
}

#site-icon #icon-hexagon{
  fill: var(--site-main-color);
}

@media (max-width: 400px) {
  #header-title {
    display: none;
  }
}

//SEARCH BAR
.search-form{
  display: flex;
  margin-left: auto;
  background: var(--sb-base-color);
  padding: var(--sb-inner-padding);
  height: var(--sb-height);
  --total-height: calc(var(--tag-h) + var(--sb-inner-padding) * 2 );
  --sb-corner-offset: calc(var(--total-height) / tan(var(--sb-edge-angle)));
  clip-path: polygon(
      var(--sb-corner-offset) 0, 
      100% 0, 
      calc(100% - var(--sb-corner-offset)) 100%,
      0 100%     
  );
}

.search-inner{
  display: flex;
  align-items: center;
  background: var(--sb-base-color);
  --sb-corner-offset: calc(var(--sb-height) / tan(var(--sb-edge-angle)));
  clip-path: polygon(
     var(--sb-corner-offset) 0, 
     100% 0, 
     calc(100% - var(--sb-corner-offset)) 100%,
     0 100%     
  );
}

#search-input {  
  border: none;
  outline: none;
  font-size: 1.1rem;
  height: 100%;
  width: 100%;
  font-family: "Bebas Neue", sans-serif;
  background: transparent;
  padding: 0.2rem 0.5rem;
  margin-top: 2px;
}

.search-form:hover, .search-inner:hover{
  background: var(--sb-hover-color);
}

.search-form:has(#search-input:focus){
  background: var(--sb-focus-bd-color);
}

.search-inner:has(#search-input:focus){
  background: var(--sb-hover-color);
}