main {
  flex-grow: 1;
  padding: 1rem;
  padding-top: 0;
  display: flex;
  justify-content: center;
  gap: 1rem;
  max-width: var(--mc-max-width);
  width: 100%;
  box-sizing: border-box;
}

html{
  font-family: "Roboto Condensed", sans-serif;
  scroll-padding-top: var(--content-top-offset); //Anchor link offset for sticky header
}

h1, h2, h3, h4, h4{
  font-weight: 400;
  color: var(--site-main-color);
}

.main-content-wrapper{
  flex: 1;
  min-width: 0;
}

article{
  margin-block-start: 1.5rem;
  margin-block-end: 1.5rem;
}

.hidden{
  display: none;
}

//BREADCRUMBS
#breadcrumbs{
  display: flex;
  margin-block-start: 1.5rem;
  margin-block-end: 1.5rem;
  font-weight: 450;
  align-items: center;
  gap: 0.5rem;
  color: var(--site-main-color);
}
#breadcrumbs.hidden, #breadcrumbs.b-hidden{
  display: none;
}

#breadcrumbs .arrow{
  width: 0.8rem;
  height: 0.8rem; 
  flex-shrink: 0;
  fill: var(--color-gray5);
}

#breadcrumbs a{
  color: var(--color-gray5);
  text-decoration: none;
}

#breadcrumbs a:hover{
  color: var(--site-main-color2);
}

//SCROLL TO TOP BUTTON
#btn-scroll-top{
  --hex-size: 3rem;
  display: none;
  position: fixed;
  border: none;
  width: calc(var(--hex-size) * sin(30deg) / sin(120deg) );
  height: var(--hex-size);
  border-radius: calc(var(--hex-size)* 0.1) / calc(var(--hex-size) * 0.1 * 0.4);   
  background: var(--color-gray1);
  opacity: 0.8;
  filter: drop-shadow(0 0 4px rgba(0, 0, 0, 0.5));
  cursor: pointer;

  bottom: 1.5rem; 
  right: max(2rem, calc((100vw - var(--mc-max-width)) / 2 + var(--sidebar-w) - var(--hex-size)));
  z-index: 100;

  transform: translatey(0px);
	animation: float 4s ease-in-out infinite;
  transition: opacity 0.2s ease;
}

#btn-scroll-top:hover{
  animation-play-state: paused;
  opacity: 1;
}

@keyframes float {
	0% { transform: translatey(0px);}
	50% { transform: translatey(-10px);}
	100% {transform: translatey(0px);}
}

@media (hover:none){
  #btn-scroll-top{
    animation: none;
    opacity: 1;
  }
}

#btn-scroll-top::before, 
#btn-scroll-top::after{
  content: "";
  position: absolute;
  background: inherit;
  top: 0;
  left: 0;
  width: inherit;
  height: inherit;
  border-radius: inherit;
}
#btn-scroll-top::before{
  transform: rotate(60deg);
}
#btn-scroll-top::after{
  transform: rotate(120deg);
}

#btn-scroll-top i{
  color: var(--site-main-color);
  position: absolute;
  translate: -50% -50%;
  z-index: 9999;
  font-size: 1.2rem;
}

#btn-scroll-top:hover i{
  color: var(--site-main-color2);
}