:root{
    --tag-h: 1.875rem;
    --tag-edge-angle: var(--angle);
    --tag-bg-base: var(--color-gray1);
    --tag-bg-hover: var(--color-gray2);
    --tag-fg-base: var(--site-main-color);
    --tag-fg-hover: var(--site-main-color2);
}

#tags{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem 0.5rem;
}

.tag{
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    text-decoration: none;
    color: var(--tag-fg-base);
    background: var(--sb-base-color);
    --padding-vertical: 0.2rem;
    padding: var(--padding-vertical) 1rem;
    height: var(--tag-h);   
    --total-height: calc(var(--tag-h) + var(--padding-vertical) * 2 );
    --sb-corner-offset: calc(var(--total-height) / tan(var(--tag-edge-angle)));
    
    clip-path: polygon(
        var(--sb-corner-offset) 0, 
        100% 0, 
        calc(100% - var(--sb-corner-offset)) 100%,
        0 100%     
    );
}

.tag span:first-child{
    margin-right: 0.5rem;
}

.tag:hover{
    color: var(--tag-fg-hover);
    background: var(--tag-bg-hover);
}